@import "../../assets/style/index.scss";

.diplomas-container {
  padding-bottom: 100px;
  
  .diplomas-box {
    background: #F9F9F9;
    
    h2 {
      margin-bottom: 50px;
      color: $color-black;
      font-family: $family-manrope;
      font-style: normal;
      font-weight: 600;
      line-height: 120%;

      @include onMobile {
        margin-bottom: 24px;
      }
    }

    .slider-container {
      margin: 0 auto;
      // width: 100%;

      .img-box {
        // width: 100%;
        aspect-ratio: 1;
        display: inline-block;

        img {
          // width: 25%;
          // display: flex;
          // margin: 0 auto;
          object-fit:contain;
          width: 100%;
          height: 100%;
          // align-self: center;
        }
      }
    }

    .popup {
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.8);
      display: flex;
      justify-content: center;
      align-items: center;
      z-index: 9999;

      .popup-img {
        max-width: 90%;
        max-height: 90%;
        border-radius: 5px;
      }
    }
  }
}